@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
  font-family: "Cafe24Simplehae";
  background-color: #eeeeee;
  font-weight: bolder;
  margin-right: 0;
  height: 100vh; /* 뷰포트의 높이만큼 차지하도록 설정 */
}
header {
  background-color: white;
  text-align: left;
  padding: 20px;
  font-size: 30px;
  margin-bottom: 20px;
}
#index{
  font-size: 30px;
  margin-left : 60px; 
}
.container {
  display: grid;
  grid-template-columns: 450px 1fr;
  height: 100%; /* 부모 요소의 높이에 맞게 설정 */
}
nav {  
  font-family: "NanumBarunpen";
  margin-right: 20px;
  font-size: 30px;
  background: #f9f9f9;
}
nav a {
  text-decoration: none;
  color: #333;
  padding: 10px 20px;
  margin: 10px 0;
}

nav a:hover {
  background-color: #333;
  color:#e9e9e9;
}
main{
  font-family: "Nanum gothic";
  margin-left: 20px;
  background-color: #f9f9f9;
  font-size: 30px;
}
footer {
  text-align: center;
  background-color: gray;
  color: white;
  font-size: 40px;
}
a:link, a:visited {
  background-color:#eeeeee; 
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
  width:300px;
  font-size: 1em;
}
a:hover, a:active { 
  background-color: #FF4500; 
  color:white ;
}
.dark-mode {
  background-color: #333;
  color: #e9e9e9;
}
.dark-mode a:hover, a:active {
  background-color: #333;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
  width:300px;
  font-size: 1em;
}
.dark-mode nav {
  background: #333;
}

.dark-mode nav a {
  color: #333;
}

.dark-mode nav a:hover {
  background: #69b4b9;
}

.dark-mode header {
  background-color: #333;
}
.dark-mode main{
  background-color: #333;
}
button1 {
  position: fixed;
  bottom: 20px;
  right: 20px;
  padding: 10px 20px;
  background-color: #f9f9f9;
  color: #333;
  border: none;
  cursor: pointer;
}
button1:hover{
  background-color: #0e0d0d;
  color: #e9e9e9;
}
button {
  font-family: "NanumBarunpen";
  background-color: darkgreen;
  color: white ;
  padding: 20px 30px;
  margin: 20px; 
  font-size: 2em;
  cursor: pointer;
  transition-duration: 0s;
}
button:hover {
  color: white;
}
