.login-container {
    display: flex;
    flex-direction: column;
    width: 300px; /* 변경된 크기 */
    height: 300px; /* 변경된 크기 */
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  input {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button.login-button {
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .login-message {
    margin-top: 10px;
    color: green;
  }